<template>
  <div class="hamburger-menu">
    <div
      id="nav-icon1"
      class="hvr-wobble-vertical"
      :class="[open ? 'open' : '']"
      @click="toggleMenu()"
    >
      <span />
      <span />
      <span />
      <span />
    </div>
    <div 
      class="logo"
      :class="[open ? 'open' : '']"
    >
      <!-- <font-awesome-icon class="pr-1" icon="stroopwafel" /> -->
      <span>tmstories</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HamburgerMenu',
  props: {
    openMenu: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      open: false
    }
  },
  watch: {
    openMenu(val) {
      this.open = val;
    }
  },
  methods: {
    toggleMenu() {
      this.open = !this.open
      this.$emit('menu', this.open)
    }
  }
}
</script>

